<!-- 各种记录 -->
<template>
	<view>
		<view class="ul">
			<view class="li" v-for="(k,i) in 4">
				<view class="title">
					<view class="time">
						2021-6-5
					</view>
					<view class="lineDay" v-show="isShowDeadLine">
						<text>2022-6-6</text>到期
					</view>
				</view>
				<view class="content">
					<view class="option" v-for="(k,i) in 3">
						<view class="key">投保公司：</view>
						<view class="value" :style="{'maxWidth':i==0?'280rpx':'500rpx'}">交强险、第三者责任险、车辆损失险、车上人员责任险-司机、车上人员责任险-乘客</view>
					</view>
					<view class="money" v-if="isShowSum">
						金额：<view> <text>10000</text>元 </view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: '(标题)',
				isShowDeadLine: null,
				isShowSum: null,
				// money: null,
			}
		},
		methods: {
			judgeType(key) {
				if (key === "保险") {
					this.isShowDeadLine = true
					this.isShowSum = true
				} else if (key === "保养") {
					this.isShowDeadLine = false
					this.isShowSum = true
				} else if (key === "出险") {
					this.isShowDeadLine = false
					this.isShowSum = false
				} else if (key === "维修") {
					this.isShowDeadLine = false
					this.isShowSum = true
				}
			}
		},
		onLoad(option) {
			this.title = option.name + '记录'
			this.judgeType(option.name)
		}
	}
</script>

